section#portfolio {
  margin-top: 0rem;
  position: relative;
}
#dropdown-basic {
    display: flex;
    border-color: var(--color-primary-variant);
    background: var(--color-bg-variant);
    color: var(--color-white);
    width: 15rem;
    font-size: 33px;
    margin: 30px auto;
    justify-content: center;
    align-items: center;
    border-radius: 6px;
    cursor: pointer;
}
.arrowExplore {
  border-radius: 50%;
  margin-left: 15px;
  transform: rotate(0deg);
  transition: ease-in-out 0.5s;
}
.toggled {
  transform: rotate(180deg);
  transition: ease-in-out 0.5s;
}
.dropdown-menu {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 50px;
  height: 0;
  transition: ease-in-out 0.5s;
  opacity: 0;
  visibility: hidden;
}
.dropdown-toggled {
  height: 5rem;
  opacity: 1;
  visibility: visible;
  transition: ease-in-out 0.5s;
}
.tabs {
  border-color: var(--color-primary-variant);
  background: transparent;
  color: var(--color-primary);
  border-radius: 2rem;
  border: 1px solid;
  transition: var(--transition);
  transition: var(--transition);
  width: 15rem;
  font-size: 33px;
  margin-left: 5px;
  margin-top: 10px;
  height: 4rem;
  text-align: center;
   cursor: pointer;
}
.tabs:hover {
  background: var(--color-primary);
  color: var(--color-bg);
}
.active-tabs {
  background: var(--color-primary);
  color: var(--color-bg);
  border-radius: 2rem;
  transition: var(--transition);
  width: 15rem;
  font-size: 33px;
  margin-left: 5px;
  margin-top: 10px;
  height: 4rem;
  text-align: center;
  transition-property: border background;
  transition: 0.5s all ease-in-out;
}
.content {
  display: none;
}
.active-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    grid-gap: 28.5rem;
    gap: 2.5rem;
}
.projects-number {
    color: var(--color-primary);
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    width: 100%;
    margin-top: 75px;
    margin-bottom: 20px;
    font-size: 21px;
}
video {
  width: 100%;
  height: 100%;
}
.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1.3rem;
    border-radius: 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
    width: 50%;
}
.portfolio__item {
  z-index: 1;
  background-size: contain;
}
.portfolio__item:hover {
  border: 1px solid var(--color-primary);
  background: transparent;
 }
 .portfolio__item-image {
  border-radius: 1.5rem;
  overflow: hidden;
  transition-property: transform;
}
.portfolio__item-image:hover {
  transform: scale(1.2);
  transition: ease-in-out .5s;
}
 .portfolio__item-details {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  grid-gap: 28px;
  margin-top: 37px;
}
.portfolio__item-cta {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  margin-bottom: 0.5rem;
}
.portfolio__item-cta a {
  width: 131px;
  margin-top: 24px;
}
.portfolio__item-details h6 {
  font-size: 33px;
  color: var(--color-white);
}
.portfolio__item-details p {
  font-size: 17px;
  color: var(--color-white);
}
.portfolio__item:hover
.portfolio__item-details h6 {
  color: var(--color-primary);
}
.portfolio__item:hover
.portfolio__item-details p {
  color: var(--color-primary);
}
.portfolio__item:hover
.portfolio__item-details p {
  color: var(--color-primary);
}
.search__widget, .sorting__widget select {
  padding: 0px 14px;
  width: 50%;
  border-radius: 5px;
  cursor: pointer;
  margin: 15px auto;
}
.search__widget input {
  width: 70%;
  height: 20px;
}
.search__widget input:focus {
  outline: none;
}
ul.paginationBttns {
  display: flex;
  justify-content: space-evenly;
  margin-top: 30px;
  color: var(--color-white);
}
ul.paginationBttns li {
  padding: 7px;
  background-color: var(--color-primary);
  border-radius: 3px;
}

/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/

@media screen and (max-width: 1024px) {
.portfolio__container {
  grid-template-columns: lfr lfr;
  gap: 1.2rem;
}
}
/* ===================MEDIA QUERIES (SMALL DEVICES) =============================*/
@media screen and (max-width: 768px) {
.content.active-content {
    margin: auto;
}
.portfolio__item-details p {
  font-size: 15px;
}
.search__widget {
  margin-bottom: 20px;
}
.search__widget input {
  font-size: 0.8rem;
  width: 170px;
}
}
