.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  border-radius: 2rem;
  background: linear-gradient(45deg, transparent, var(--color-primary),
 transparent
);
  display: grid;
  place-items: center;
}
.about__me-image {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: var(--transition);
}

.about__me-image:hover {
  transform: rotate(0);
}
.about__cards {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
}
.about__card {
  background: var(--color-bg-variant);
  border: 1px solid var(--color-bg-variant);
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 20px;
  text-align: center;
  transition: var(--transition);
}
.about__card:hover {
  transform: translateY(-15px);
  border: 1px solid  var(--color-primary);
  cursor: default;
  transition: ease-in-out 0.5s;
}
.about__card:hover .about__icon {
    border: 1px var(--color-bg);
    font-size: 47px;
    padding: 7px;
    color: var(--color-bg);
    border-radius: 50%;
    margin-bottom: 2rem;
}
.about__icon {
    background: var(--color-primary);
    font-size: 47px;
    padding: 7px;
    color: var(--color-bg);
    border-radius: 50%;
    margin-bottom: 2rem;
}
.about__card h5 {
  font-size: 26px;
}
.about__card small {
  font-size: 25px;
  color: var(--color-light);
}
.about__content p {
  margin: 2rem 0px 2.6rem;
  color: var(--color-light);
  font-size: 27px;
  width: 40rem;
}

/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/
@media screen and (max-width: 1280px) {
.about__me {
  width: 39%;
  margin-left:205px;
  margin-bottom: 48px;
}
.about__card {
  margin-bottom: 20px;
}
}
@media screen and (max-width: 1024px) {
.about__container {
  grid-template-columns: 1fr;
  gap: 0;
}
.about__me {
  width: 50%;
  margin: 2rem auto 4rem;
}
.about__content p {
  margin: 1rem 0 1.5rem;
}
.about__card {
  margin-bottom: 20px;
}
}
