@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@600;700&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  scroll-padding-top: 2rem;
  scroll-behavior: smooth;
  list-style: none;
  text-decoration: none;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
:root {
  --color-bg: #fff!important;
  --color-bg-variant: #755fa7a6;
  --color-bg-variant-hover: #755fa7a6;
  --color-primary: #755fa7a6;
  --color-primary-variant: rgba(133,77,255,.41);
  --color-white: #fff;
  --color-light: hsla(0,0%,100%,.88);
  --submit-color: #aa92b6;
  --shadow-color: 0 0 15px 15px rgba(132, 29, 200, 0.16);
  --transition: all 400ms ease;
  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90vw;
}
h1, h2, h3, h4, h5 {
  font-weight: 500;
}
h1, h2, h3, h4, h5, h6 {
  color: var(--color-white);
}
p {
  color: var(--color-primary);
  font-size: 16px;
}
img {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.btn, a {
  cursor: pointer;
  transition: var(--transition);
}
a {
  align-items: center;
  border-radius: .7rem;
  display: flex;
  justify-content: center;
  text-align: center;
}
.btn {
  background: var(--color-white);
  border: 1px solid var(--color-primary);
  border-radius: .4rem;
  color: var(--color-primary);
  display: inline-block;
  font-size: 29px;
  padding: .75rem 1.2rem;
  width: -webkit-max-content;
  width: max-content;
}
.btn-primary {
  background-color: var(--color-primary);
  border: 1px solid var(--color-white);
  color: var(--color-white);
}
.btn-primary:hover {
  background-color: var(--color-white);
  border: 1px solid var(--color-primary);
  color: var(--color-primary);
}
.btn-secondery:hover {
  background-color: var(--color-primary);
  border: 1px solid var(--color-white);
  color: var(--color-white);
}
section {
  margin-top: 6rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
}
.full-content {
  display: flex;
  flex-direction: row;
}
.icon-branch {
    background: var(--color-primary);
    font-size: 47px;
    padding: 7px;
    color: var(--color-bg);
    border-radius: 50%;
    margin-bottom: 5px;
}
section div h2 {
  margin-bottom: 3rem;
}
section div h2, section div h5 {
  color: var(--color-primary);
}
section div h2, section div h5 {
  font-size: 22px;
  text-align: center;
}
.container {
  margin: 0 auto;
  overflow: hidden;
  width: 75%;
  width: var(--container-width-lg);
}
.text-light {
  color: var(--color-light);
  font-size: 25px;
}
.loader-container {
  width: 100%;
  height: 100%;
}
.loader-container {
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: var(--color-white);
}
.loader {
  width: 48px;
  height: 48px;
  border: 3px solid var(--color-bg-variant-hover);
  border-radius: 50%;
  display: inline-block;
  position: relative;
  box-sizing: border-box;
  animation: rotation 1s linear infinite;
} 
.loader::after {
  content: '';  
  box-sizing: border-box;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 56px;
  height: 56px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-bottom-color: var(--color-primary-variant);
}
input, textarea {
  font-size: 24px;
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: var(--color-primary-variant);
}
@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@media screen and (max-width: 1024px){
.container {
    width: 90%;
}
}
@media screen and (max-width: 375px){
.container {
    width: 95%;
}
}
