main {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-top: 3rem;
    overflow: hidden;
    background: transparent;
    border: 1px solid var(--color-primary);
    background-image: url(../../assets/shape-background.png);
    background-repeat: no-repeat;
    background-position: center;
  }
  main:hover {
    background-color: transparent;
  }
.main__container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding-bottom: 68px;
    position: relative;
    width: 100%;
  }
  .main__container div {
    display: flex;
    justify-content: center;
    align-items: center;
}
  .main__container div h5, .main__container div h1 {
    color: var(--color-primary);
    font-size: 25px;
  }
  .main__container div h5 {
    margin-top: 6rem;
  }
  .main__container div h1{
    top: 44px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.hand-wave {
  animation: shake 1s linear infinite;
}
@keyframes shake {
  10% {
    transform: rotate(15deg);
  }
  20% {
    transform: rotate(-15deg);
  }
  30% {
    transform: rotate(15deg);
  }
  40% {
    transform: rotate(-15deg);
  }
}
  /*==================CTA=====================*/
  .cta a {
    font-size: 15px;
  }
  /*==================SCROLL-DOWN=====================*/
  
  .scroll__down {
    width: 79px;
    margin-right: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
