header {
  display: flex;
  flex-direction: row;  
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
  background: var(--color-bg-variant);
  border: 1px solid var(--color-bg-variant-hover);
}
.header__container {
  display: flex;
  padding-top: 20px;
  flex-direction: row;
  align-items: center;
  height: 100%;
  position: relative;
  justify-content: space-around;
  width: 100%;
  margin: 0 auto;
}
.profile h1 {
    font-size: 25px;
}
/*==================HEADER-SOCIALS=====================*/

.header__icons {
  display: flex;
  flex-direction: row-reverse;  
  align-items: center;
  justify-content: space-between;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  font-size: 32px;
}
.header__icons .icon {
    background: var(--color-bg-variant-hover);
    outline: 2px solid var(--color-white);
    border-radius: 50%;
    transition-property: outline-offset, outline-color,background-color;
    transition-duration: 0.25s;
    border: 1px solid transparent;
    height: 60px;
    width: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--color-white);
    cursor: pointer;
}
.header__icons::after {
  content: '';
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}
.header__icons .icon:hover {
  background: transparent;
  border: 1px solid var(--color-white);
  outline-offset: 4px;
}
.header__icons a .icon {
  margin: auto;
  font-size: 27px
}
.header__icons a:hover .icon {
  animation: shake 0.25s;
}
@keyframes shake {
  10% {
    transform: rotate(15deg);
  }
  20% {
    transform: rotate(-15deg);
  }
  30% {
    transform: rotate(15deg);
  }
  40% {
    transform: rotate(-15deg);
  }
}
.profile {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
/*==================ME=====================*/

.me {
    background: var(--color-bg-variant-hover);
    width: 170px;
    height: 170px;
    margin-inline: 5px;
    border-radius: 50%;
    overflow: hidden;
    padding: -1rem 1rem;
}

.me::after {
  border-top: medium;
  content: '';
  box-sizing: border-box;
  display: flex;
  position: absolute;
  justify-content: center;
  align-self: center;
  margin-top: -170px;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  border: 20px solid transparent;
  border-bottom-color: var(--color-primary);
  animation: rotation 4s linear infinite;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
/* ===================MEDIA QUERIES (MEDIUM DEVICES) =============================*/
